@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
:root {
   --primary-color: #ff9600;
   --secondary-color: #411867;
   --light-color: #808e9b;

   --navbar-height: 90px;

   --orange-color: #ff9600;

   --text-color-primary: black;
}

/* page reset */
* {
   font-family: "Open Sans", sans-serif;
   box-sizing: border-box;
   list-style-type: none;
   padding: 0;
   margin: 0;
}

/* Default Styling */
a {
   text-decoration: none;
}

/* Primary Styling */
.container {
   width: 80%;
   margin: 0 auto;
}

.flex {
   display: flex;
   justify-content: center;
   align-items: center;
}

.primary-color {
   color: #ff9600;
   color: var(--orange-color);
   text-shadow: 0px 0px 2px grey;
}

.section-header {
   letter-spacing: 3px;
   text-align: center;
}

/* Store Items */

.store {
   width: 90%;
   margin: 50px auto;
   border: 1px solid pink;
}

/* Button */
.btn {
   padding: 8px 20px;
   border: 2px;
   outline: none;
   border: none;
   cursor: pointer;
   border-radius: 10px;
}

.btn-primary {
   background-color: #411867;
   background-color: var(--secondary-color);
   color: white;
}

.btn-secondary {
   border: 3px solid #411867;
   border: 3px solid var(--secondary-color);
   background: transparent;
   color: #411867;
   color: var(--secondary-color);
}

.btn-medium:hover,
.btn-large:hover {
   background-color: white;
   color: #242424;
   transition: all 0.3s ease-out;
}

/* Product Section */

.product-section {
   height: 850px;
   width: 100%;
   padding: 100px;
   background-color: lightgrey;
}

.product-section .container > h1 {
   text-align: center;
   margin-top: -40px;
   margin-bottom: 30px;
   font-size: 2.5rem;
   text-transform: uppercase;
}

/* supplement section */

.supplement-section {
   background: url("/Athletic-Shop/img/runner.jpg") center;
   width: 100%;
   position: relative;
   height: 640px;
   z-index: 1;
   display: flex;
   flex-direction: row;
   align-items: center;
   justify-content: center;
}

.supplement-overlay {
   width: 100%;
   height: 640px;
   background-color: black;
   opacity: 0.1;
   z-index: -1;
   position: absolute;
}

.supplement-content {
   padding: 80px 60px;
   color: white;
}

.supplement-content h1 {
   font-size: 4rem;
   text-transform: uppercase;
}

.supplement-content p {
   font-size: 1.1rem;
   margin-bottom: 20px;
}

.supplement-content Button {
   border: 4px solid #ff9600;
   border: 4px solid var(--orange-color);
   text-transform: uppercase;
   color: white;
   font-weight: 700;
   font-size: 1.5rem;
}

.supplement-products {
   display: flex;
   margin-right: 20px;
}

.shopping-cart {
   top: 50%;
   right: 25px;
   transform: translateY(-50%);
   position: absolute;
}

.shopping-cart i {
   color: black;
   font-size: 25px;
}

.items-in-cart {
   background-color: #ff9600;
   background-color: var(--primary-color);
   text-align: center;
   width: 15px;
   height: 15px;
   border-radius: 50%;
   font-size: 10px;
   margin-top: -32px;
   margin-left: 20px;
   position: absolute;
   color: white;
   z-index: 100;
}

/* Store page styling */

.store-page {
   padding: 2rem;
}

.featured-items {
   width: 80%;
   margin: 0 auto;
   display: flex;
   justify-content: space-between;
   padding: 2rem;
}

.new-items {
   padding: 2rem;
   width: 80%;
   margin: 0 auto;
   display: flex;
   justify-content: space-between;
}

/* Hero  */

.hero {
   width: 100%;
   overflow: hidden;
   position: relative;
}

.hero-slides {
   display: flex;
}

.hero-slides input {
   display: none;
}

.slide {
   width: 100%;
   transition: 2s;
}

.slide img {
   width: 1920px;
   height: 800px;
}

/* Start of Manual Navigation */

.navigation-manual {
   position: absolute;
   width: 100%;
   bottom: 30px;
   display: flex;
   justify-content: center;
}

.manual-btn {
   border: 2px solid var(--primary-color);
   padding: 5px;
   border-radius: 10px;
   cursor: pointer;
   transition: 0.5s;
}

.manual-btn:not(:last-child) {
   margin-right: 10px;
}

.manual-btn:hover {
   background-color: var(--primary-color);
}

/* Start of Radio styling */

#radio1:checked ~ .first {
   margin-left: 0;
}

#radio2:checked ~ .first {
   margin-left: -100%;
}

#radio3:checked ~ .first {
   margin-left: -200%;
}

#radio4:checked ~ .first {
   margin-left: -300%;
}

/* End  of Radio styling */

/* End of Manual Navigation */

/* Start of Automatic navigation */

.navigation-auto {
   position: absolute;
   display: flex;
   width: 100%;
   bottom: 30px;
   justify-content: center;
}

.navigation-auto div {
   border: 2px solid var(--primary-color);
   padding: 5px;
   border-radius: 10px;
   transition: 0.5s;
}

.navigation-auto div:not(:last-child) {
   margin-right: 10px;
}

/* Start of Automatic radio buttons */

#radio1:checked ~ .navigation-auto .auto-btn1 {
   background: var(--primary-color);
}

#radio2:checked ~ .navigation-auto .auto-btn2 {
   background: var(--primary-color);
}

#radio3:checked ~ .navigation-auto .auto-btn3 {
   background: var(--primary-color);
}

#radio4:checked ~ .navigation-auto .auto-btn4 {
   background: var(--primary-color);
}

/* End of Automatic radio buttons */

/* End of Automatic navigation */

.hero-overlay {
   position: absolute;
   width: 100%;
   top: 50%;
   transform: translateY(-50%);
   display: flex;
   align-items: center;
   justify-content: center;
   flex-direction: column;
   z-index: 100;
}

.hero-overlay-title {
   font-size: 7rem;
   font-weight: 1100;
   text-shadow: 1px 1px 1px black;
   color: var(--primary-color);
}

.hero-overlay-sub-title {
   color: white;
   margin-top: -20px;
   margin-bottom: 5px;
   text-shadow: 1px 1px 1px black;
}

.hero-overlay Button {
   border-color: var(--secondary-color);
   font-size: 1.2rem;
   text-transform: uppercase;
}

/* Product Cards */

.product-card {
   height: auto;
   padding-bottom: 20px;
   width: 355px;
   border-radius: 25px;
   box-shadow: 0px 0px 8px grey;
   overflow: hidden;
   background-color: white;
}

.product-cards {
   justify-content: space-between;
}

.product-info {
   background-color: white;
   padding: 25px;
   border-bottom: 1px solid grey;
   text-align: center;
}

.product-img {
   width: 100%;
   height: calc(515px - 120px);
}

.product-title {
   font-size: 0.9rem;
   text-transform: uppercase;
   margin-bottom: 8px;
}

.product-price {
   margin-bottom: 8px;
   font-size: 1.8rem;
   font-weight: 200;
   color: var(--primary-color);
}

.product-description {
   color: grey;
   font-size: 0.8rem;
   text-transform: uppercase;
}

.product-button {
   text-align: center;
}

.product-button button {
   background-color: var(--primary-color);
   color: var(--text-color-primary);
}

/* promotion item styling */

.promo-section {
   padding: 100px;
}

.promo-header {
   text-align: center;
   margin-top: -40px;
   margin-bottom: 30px;
   text-transform: uppercase;
   letter-spacing: 2px;
   font-size: 2.5rem;
}

.promo-cards {
   justify-content: space-between;
}

.promo-card {
   height: 230px;
   width: 400px;
   position: relative;
   border-radius: 25px;
   box-shadow: 0px 0px 8px grey;
   overflow: hidden;
}

.promo-img {
   width: 100%;
   height: 100%;
   position: absolute;
   z-index: -1;
}

.promo-content {
   height: 100%;
   width: 100%;
   display: flex;
   flex-direction: column;
   padding: 50px 40px;
   text-shadow: 0px 1px 3px grey;
}

.promo-title {
   color: white;
   font-size: 1.1rem;
   margin-bottom: -5px;
}

.promo-sub-title {
   color: white;
   margin-bottom: 10px;
   font-size: 1.5rem;
}

.promo-content Button {
   background-color: var(--secondary-color);
   color: white;
   text-transform: uppercase;
   font-weight: 600;
   font-size: 1rem;
}

/* Navbar styling */

.navbar {
   height: 80px;
   width: 100%;
   background-color: white;
   box-shadow: 0px 0px 5px grey;
   z-index: 100;
   position: fixed;
   display: flex;
   align-items: center;
}

.nav-logo {
   position: absolute;
   margin-left: 20px;
   font-size: 2rem;
   letter-spacing: 3px;
   color: var(--secondary-color);
   font-weight: 800;
}

.navbar-list {
   display: flex;
   flex-direction: row;
   justify-content: flex-end;
   align-items: center;
   height: 80px;
}

.navbar-list li {
   margin-right: 20px;
}

.nav-item a {
   margin-right: 20px;
   color: var(--secondary-color);
   text-transform: uppercase;
}

.nav-item a:hover {
   border-bottom: 2px solid var(--orange-color);
   color: var(--orange-color);
}

.active {
   color: var(--primary-color);
}

